import React from 'react';
import { Menu, Form, Container } from 'semantic-ui-react';
// import firebase from 'firebase';
// require('firebase/auth');
import 'firebase/compat/auth';
import firebase from '../database/firebase';
import { useNavigate } from 'react-router-dom';
function Signin() {
    const navigate = useNavigate();
    const [activeItem, setActiveItem] = React.useState("register");
    const [account, setAccount] = React.useState("");
    const [password, setPassword] = React.useState("");
    function Submit() {
        if (activeItem === 'register') {
            firebase.auth().createUserWithEmailAndPassword(account, password).then(() => {
                alert('');
                navigate('/');
            });
        }
        else if (activeItem === 'signin') {
            firebase.auth().signInWithEmailAndPassword(account, password).then(() => {
                alert('');
                navigate('/');
            });
        }
    }
    return <>
        <Container>
            <Menu widths={2}>
                <Menu.Item active={activeItem === 'register'}
                    onClick={() => setActiveItem("register")}></Menu.Item>
                <Menu.Item active={activeItem === 'signin'}
                    onClick={() => setActiveItem("signin")}></Menu.Item>
            </Menu>
            <Form onSubmit={Submit}>
                <Form.Input label="/" value={account}
                    onChange={(e) => setAccount(e.target.value)}
                    placeholder="">
                </Form.Input>
                <Form.Input label="" value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    placeholder="" type='password'>
                </Form.Input>
                <Form.Button>
                    {activeItem === 'register' && ''}
                    {activeItem === 'signin' && ''}
                </Form.Button>
            </Form>
        </Container>
    </>;
}
export default Signin;